@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap);
* {
  box-sizing: border-box;
  color: white;
  color: var(--text-color);
  font-family: Poppins , sans-serif;
  margin: 0;
  outline: none;
  padding: 0;
  text-decoration: none;
}

/* <-------- ROOT --------> */
:root {

  --bg-color: #1b2430;

  --alt-bg-color: white;

  --text-color: white;

  --alt-text-color: #12171d;

  --primary-color: #f2ebe9;

  --secondary-color: #816797;
}

body {
  background-color: #1b2430;
  background-color: var(--bg-color);
  background-origin: content-box;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
}

.App {
  text-align: center;
}

.App-logo {
  height: 15vmin;
  pointer-events: none;
}

.red-border {
  border: 1px solid red;
}

.green-border {
  border: 1px solid green;
}

@media ( prefers-reduced-motion : no-preference ) {

  .App-logo {
    -webkit-animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
            animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  }
}

.App-header {
  align-items: center;
  background-color: #282c34;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  min-height: 100vh;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

@keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

.text-center {
  color: var(--text-color);
  display: block;
  font-size: 23px;
  margin: -50px 0 80px;
  text-align: center;
  text-transform: uppercase;
}

.login__page {
  background-color: rgba(0, 0, 0, 0.89);
  border-radius: 3px;
  left: 50%;
  padding: 100px 70px;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.input-container {
  margin-bottom: 25px;
  position: relative;
}

.input-container label {
  color: var(--text-color);
  font-size: 16px;
  left: 0;
  position: absolute;
  top: 0;
  transition: all 0.5s ease-in-out;
}

.input-container input {
  background: transparent;
  border: 0;
  border-bottom: 1px solid #555;
  color: var(--text-color);
  font-size: 16px;
  padding: 8px 0 5px;
  width: 100%;
}

.input-container input:focus {
  border: none;
  border-bottom: 1px solid #e74c3c;
  outline: none;
}

.btn {
  background-color: #e74c3c;
  border: 0;
  border-radius: 2px;
  color: var(--text-color);
  cursor: pointer;
  outline: none;
  padding: 10px 20px;
  position: relative;
  text-transform: uppercase;
}

.mt-50 {
  margin-top: 50px;
}

.disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

.input-container input:focus ~ label, .input-container input:valid ~ label {
  font-size: 12px;
  top: -12px;
}

.gravatar-icon {
  width: 50px;
}

.question-container {
  align-items: center;
  background-color: black;
  border: 3px solid white;
  display: flex;
  flex-direction: column;
  grid-gap: 1.4rem;
  gap: 1.4rem;
  height: 100%;
  justify-content: center;
  margin: auto;
  padding: 2rem;
  width: 60%;
}

.m5 {
  background-color: black;
  border: 3px solid white;
  margin: 5px;
}

.green {
  border: 3px solid rgb(6, 240, 15);
}

.red {
  border: 3px solid red;
}

.img {
  width: 50px;
}

.rotate-center {
  -webkit-animation: rotate-center 2s ease 0s infinite;
          animation: rotate-center 2s ease 0s infinite;
}

@-webkit-keyframes rotate-center {

  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }

  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate-center {

  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }

  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.counter-container {
  align-items: center;
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  height: calc(50px + 3rem);
  justify-content: center;
}

.disabled {
  cursor: auto;
  opacity: 0.7;
}

